import { Button, Card, Divider, TextField } from "@mui/material";
import { useState } from "react";
import TextareaAutosize from "@mui/material/TextareaAutosize";
import { Stack } from "@mui/system";
import axios from "axios";
// import UnstyledInputBasic from "./s";

interface Iprops {
  idProject: string | undefined;
}

const NewTask = ({idProject}:Iprops) => {
  const [startDate, setStartDate] = useState<string>();
  const [andDate, setAndDate] = useState<string>();
  const [taskDescription, setTaskDescription] = useState<string>();

  const postNewTask=(event:any)=>{
    event.preventDefault(); 
    console.log(startDate,andDate,taskDescription);
    axios.post('http://localhost:3000/createNewTask',{projetId:idProject,taskDescription:taskDescription,startDate:startDate,andDate:andDate})
    
  }

  return (
    <>
    <form onSubmit={postNewTask}>
      <Card classes sx={{ p: 2, mt: 5, background: "#b0b0b0a1" }}>
        <Stack spacing={3}>
          {/* <UnstyledInputBasic /> */}
          <TextareaAutosize
            required
            aria-label="maximum height"
            placeholder={" ..."}
            style={{ width: 500, height: 150 }}
            onChange={(e) => {
              setTaskDescription(e.target.value);
            }}
          />
          <Stack
            direction="row"
            spacing={2}
            divider={<Divider orientation="vertical" />}
          >
            <TextField
              label=" "
              variant="outlined"
              required
              value={startDate}
              // error={!a}
              onChange={(e) => {
                setStartDate(e.target.value);
              }}
              size={"small"}
              type="text"
              // helperText={!a && " "}
            />
            <TextField
              label=" "
              variant="outlined"
              required
              value={andDate}
              // error={!a}
              onChange={(e) => {
                setAndDate(e.target.value);
              }}
              size={"small"}
              type="text"
              // helperText={!a && " "}
            />

            <Button
              type="submit"
              variant="outlined"
              sx={{ width: 150, boxShadow: 2, mt: 3 }}
              // onSubmit={() => {}}
            >
              {" >"}
            </Button>
          </Stack>
        </Stack>
        
      </Card>
      </form>
    </>
  );
};
export default NewTask;
